<template>
    <section class="blog-post section-header-offset">
        <div class="blog-post-container container">
            <div class="blog-post-data">
                <h3 class="title blog-post-title">{{title}}</h3>
                <div class="article-data">
                    <span>{{timestamp}}</span>
                    <span class="article-data-spacer"></span>
                    <span>{{category}}</span>
                </div>
                <img :src="`https://drive.google.com/uc?export=view&id=${idGambar}`" alt="">
            </div>

            <div class="container">
            <div v-html="body"></div>

                <blockquote class="quote">
                    <p><span><i class="ri-double-quotes-l"></i></span> Lorem ipsum dolor sit amet consectetur adipisicing elit. Officia voluptates, laboriosam voluptatum quos non consequuntur nesciunt necessitatibus tempora quod inventore corporis rem nihil itaque, at provident minus aliquam veritatis. Labore?  <span><i class="ri-double-quotes-r"></i></span></p>
                </blockquote>
            

                <div class="author d-grid">
                    <div class="author-image-box">
                        <img src="../../assets/images/author.jpg" alt="" class="article-image">
                    </div>
                    <div class="author-about">
                        <h3 class="author-name">{{author}}</h3>
                        <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Eaque quis repellat rerum, possimus cumque dolor repellendus eligendi atque explicabo exercitationem id.</p>
                        <ul class="list social-media">
                            <li class="list-item">
                                <a href="#" class="list-link"><i class="ri-instagram-line"></i></a>
                            </li>
                            <li class="list-item">
                                <a href="#" class="list-link"><i class="ri-facebook-circle-line"></i></a>
                            </li>
                            <li class="list-item">
                                <a href="#" class="list-link"><i class="ri-twitter-line"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import {computed, reactive, toRefs} from 'vue'
    import axios from 'axios'
    import { useStore } from 'vuex'
    import {useRoute} from 'vue-router'
    export default{
        async setup() {
            const data = reactive({
                title: '',
                category: '',
                author: '',
                body: '',
                idGambar:'',
            });
            const store = useStore();
            store.dispatch('getAllPost')
            const route = useRoute();
            const slug = route.params.slug

            // const cek = post.filter((pos)=>{
            //     return pos.slug == slug
            // })
            // console.log(cek)
            // ambil post dari database dengan id sesuai dengan id
            const result = await axios.get(`https://script.google.com/macros/s/AKfycbwo_TI09KsJDIe9SS0d9UcJNe-l49p5U5QHs2yYhKpidablaHEAKnToilY1_5PjcROG/exec?action=read&table=post&slug=${slug}`)
            .then(res => {
            return res.data.data
          })
          .catch(err => {
            console.log(err)
          })
            console.log(slug)
          // timpa data dengan result
            data.title = result.title;
            data.category = result.Category;
            data.author = result.author;
            data.body = result.body;
            data.idGambar = result.idGambar;
            return {...toRefs(data)};
        }
    }
</script>